import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Box } from "grommet";
import { Close, DisabledOutline, CaretDown, CaretUp } from "grommet-icons";
const CustomBox = styled(Box)`
  position: fixed;
  bottom: ${(props) => (props.expand ? "0" : "-360px")};
  border-radius: 4px;
  right: 10%;
`;
const Component = ({ onClick, children, expandContainer }) => {
  const [expand, setExpand] = useState(expandContainer);
  useEffect(() => {
    setExpand(expandContainer);
  }, [expandContainer]);

  return (
    <CustomBox
      expand={expand}
      border="1px"
      width="280px"
      height="400px"
      background="white"
    >
      <Box
        width="100%"
        onClick={onClick}
        direction="row"
        align="center"
        height="40px"
        pad="xsmall"
      >
        Notification
        <Box margin={{ left: "auto" }}>
          {expand ? (
            <CaretDown style={{ cursor: "pointer" }} />
          ) : (
            <CaretUp style={{ cursor: "pointer" }} />
          )}
        </Box>
      </Box>
      <Box height="100%" overflow="hidden">
        {children}
      </Box>
    </CustomBox>
  );
};

export default Component;
