import React, { useState } from "react";
import { Tools as Action } from "grommet-icons";
import { Box } from "grommet";
const Home = () => {
  const [hide, setHide] = useState(false);
  setTimeout(() => setHide(true), 4000);
  return (
    !hide && (
      <Box
        height={{ min: "300px" }}
        fill={true}
        align="center"
        justify="center"
        alignContent="center"
      >
        <Box animation="jiggle">
          <Action size="xlarge" />
        </Box>
      </Box>
    )
  );
};

export default Home;
