import React, { useEffect, useState } from "react";
import Chat from "../../components/Chat";
import { Heading } from "grommet";
import { findChatroomMessage, sendChatroomMessage } from "../../library/parse";
import EventObserver from "../EventObserver";

const Component = ({ senderRoleName, placeholder, chatroom, chatroomName }) => {
  const chatroomId = chatroom && chatroom.get("uniqueName");
  const [messages, setMessages] = useState([]);
  const [createEvent, setCreateEvent] = useState(null);

  const modelToMessage = (models) => {
    return models.map((c) => {
      return {
        message: c.message,
        senderName: c.name,
        isThirdPerson: c.role !== senderRoleName,
      };
    });
  };

  useEffect(() => {
    findChatroomMessage(chatroomId).then((e) => {
      setMessages(modelToMessage(e));
    });
  }, [chatroom]);

  useEffect(() => {
    if (createEvent) {
      findChatroomMessage(chatroomId).then((e) => {
        setMessages(modelToMessage(e));
      });
    }
  }, [createEvent]);

  return (
    <>
      {chatroomName && (
        <Heading margin="0" level={4}>
          {" "}
          {chatroomName}{" "}
        </Heading>
      )}
      <Chat
        textInputPlaceholder={placeholder}
        onSendMessage={(message) => {
          sendChatroomMessage(senderRoleName, false, message, "", chatroom.id);
        }}
        messages={messages}
      />
      <EventObserver
        objectClass={"ChatroomMessage"}
        onEvent={(e, action) => {
          if (action === "create") {
            setCreateEvent(e);
          }
        }}
      />
    </>
  );
};

export default Component;
