/* global FB */
import React, { useEffect, useState } from "react";
import {
  Anchor,
  ResponsiveContext,
  Box,
  Button,
  Menu,
  Text,
  Footer,
  TextInput,
} from "grommet";
import "../pages/index.css";
import "./i18n";
import { assign } from "../helpers";
import { Tools, Notification } from "grommet-icons";
import Parse, { getChatroomForRoles } from "../library/parse";
import Loading from "../components/Loading";
import ChatWidget from "../containers/ChatWidget";
import NewChat from "../containers/NewChat";
import ChatContext, { defaultValue } from "../context/ChatContext";
import StickyButton from "../components/Button/Sticky";
import UserContext from "../context/UserContext";
import Ad from "../containers/Ad";
import { Play } from "grommet-icons";
const Home = ({ children }) => {
  const [accountMenu, setAccountMenu] = useState([]);
  const [allowStore, setAllowStore] = useState(false);
  const [user, setUser] = useState(Parse.User && Parse.User.current());

  useEffect(() => {
    if (user && user.get("allowStore")) {
      setAllowStore(true);
    }
  }, [user]);

  useEffect(() => {
    const logoutMenu = [
      {
        label: "Dashboard",
        onClick: () => assign("/dashboard"),
      },
      // {
      //   label: "Profile",
      //   onClick: () => assign("/profile"),
      // },
      // {
      //   label: "Store",
      //   onClick: () => {
      //     assign("/account/store");
      //   },
      // },
      {
        label: "Config",
        onClick: () => {
          assign("/config");
        },
      },
      {
        label: "Sign Out",
        onClick: () => {
          Parse.User.logOut().then(() => {
            setUser(null);
            assign("/");
          });
        },
      },
    ];

    allowStore &&
      logoutMenu.push({
        label: "My Store",
        onClick: () => {
          assign("/account/store");
        },
      });
    allowStore &&
      logoutMenu.push({
        label: "System",
        onClick: () => assign("/system"),
      });

    const accMenu = !user
      ? [
          {
            label: "Sign in",
            onClick: async () => {
              assign("/login");
            },
          },
        ]
      : logoutMenu;
    setAccountMenu(accMenu);
  }, [user]);

  return (
    <Box>
      <Box
        gap="small"
        gridArea="header"
        direction="row"
        align="center"
        pad={{ horizontal: "medium", vertical: "small" }}
        background="brand"
      >
        <Box>
          <Button
            icon={<Play />}
            label="BidBid"
            onClick={() => {
              assign("/");
            }}
          />
        </Box>
        <Box>
          <TextInput placeholder="Search" size="small" />
        </Box>
        <Box direction="row" align="center" margin={{ left: "auto" }}>
          <Menu
            size="small"
            label={user ? user.get("email") : "Guest"}
            items={accountMenu}
          />
        </Box>
      </Box>

      <Box align="center" justify="center" pad="small">
        <Box
          pad={{
            bottom: "64px",
          }}
          fill="horizontal"
          width={{
            min: "360px",
          }}
        >
          {children}
          <Loading />
        </Box>
      </Box>
      <Ad />
      <Footer margin={{ top: "auto" }} background="brand" pad="medium">
        <Text>Copyright {new Date().getFullYear()} </Text>
        <ChatContext.Consumer>
          {({ openChat }) => (
            <Anchor href="https://wa.me/6588836413/">
              Need Help? Talk to Us
            </Anchor>
          )}
        </ChatContext.Consumer>
      </Footer>
    </Box>
  );
};

export default Home;
