import Parse from "../library/parse";
let liveClient;

if (!liveClient && typeof window !== `undefined`) {
  liveClient = new Parse.LiveQueryClient({
    applicationId: process.env.B4A_APP_ID,
    serverURL: process.env.B4A_LIVEQUERY_URL, // Example: 'wss://livequerytutorial.back4app.io'
    javascriptKey: process.env.B4A_JS_KEY,
    sessionToken: Parse.User.current()
      ? Parse.User.current().getSessionToken()
      : undefined,
  });
  liveClient.open();
}
export default liveClient;

export const queryEvent = (client) => {
  const query = new Parse.Query("Event");
  query.ascending("createdAt").limit(5);
  return client.subscribe(query);
};
