import { parseISO } from "date-fns";

const queryString = require("query-string");

export const isBrowser = typeof window !== `undefined`;

export const toBase64 = (str) => isBrowser && window.btoa(str);

export const fromBase64 = (str) => isBrowser && window.atob(str);

export const getQueryString = () =>
  isBrowser && queryString.parse(window && window.location.search);

export const encodeURIComponent = (str) =>
  isBrowser && window.encodeURIComponent(str);

export const assign = (url) => isBrowser && window.location.assign(url);

export const parseStringDate = (str, fallback) =>
  isNaN(parseISO(str).getTime()) ? fallback : parseISO(str);

export const chunk = (arr, len) => {
  var chunks = [],
    i = 0,
    n = arr.length;

  while (i < n) {
    chunks.push(arr.slice(i, (i += len)));
  }

  return chunks;
};

export const flattenObject = (obj, prefix = "") =>
  Object.keys(obj).reduce((acc, k) => {
    const pre = prefix.length ? prefix + "." : "";
    if (typeof obj[k] === "object" && obj[k] !== null && !Array.isArray(obj[k]))
      Object.assign(acc, flattenObject(obj[k], pre + k));
    else acc[pre + k] = obj[k];
    return acc;
  }, {});

export const replaceBracesWith = (string, lookup) => {
  return findAndReplaceUPlaceHolderKey(
    string || "",
    (w) => lookup[w] || `{${w}}`
  );
};

export const findAndReplaceUPlaceHolderKey = (text = "", func) => {
  const regex = /\{[a-zA-Z.]+\}/gi;
  const replacedResult = text.replace(regex, (p) => func(unwrap(p)));
  return replacedResult;
};

export const unwrap = (s) => s.replace("}", "").replace("{", "");

export const isSmall = (size) => ["xxsmall", "small", "xsmall"].includes(size);
