import React, { useState, useEffect } from "react";
import { Box, Anchor, Select } from "grommet";
import ContactList from "./ContactList";
import Chat from "../Chatroom/";
import {
  getRolePublicProfile,
  findChatroomWithName,
  createchatroomListForRoles,
} from "../../library/parse";
import UserContext from "../../context/UserContext";
import ChatNavigator from "./ChatNavigator";
import SelectRole from "./SelectRole";

const Component = ({ senderRole, roleList, initialChatroom, onRoleChange }) => {
  const [initalSenderRoleIndex, setInitialSenderRoleIndex] = useState(-1);
  const [activeChatroom, setActiveChatroom] = useState(initialChatroom);
  const _userContext = React.useContext(UserContext);
  const [activeSenderRoleObject, setActiveSenderRoleObject] = useState(null);
  const roles = _userContext && _userContext.roles;
  const [roleSelectionList, setRoleSelectionList] = useState([]);
  const [roleObjects, setRoleObjects] = useState(
    _userContext && _userContext.roleObjects
  );
  const [_initialChatroom, set_InitialChatroom] = useState(-1);
  const [chatroomList, setChatroomList] = useState([]);

  useEffect(() => {
    const index = roleList.findIndex(
      (e) => e.object.get("name") === senderRole
    );
    setActiveSenderRoleObject(roleList[index]);
  }, [roleList, senderRole]);

  useEffect(() => {
    if (initialChatroom) {
      //alert(JSON.stringify(chatroomList && chatroomList[0].chatroom.id))
      const index = chatroomList.findIndex(
        (e) => e.chatroom.id === initialChatroom.id
      );
      set_InitialChatroom(index);
    }
  }, [initialChatroom]);

  useEffect(() => {
    if (activeSenderRoleObject) {
      const loadchatroomList = async () => {
        const chatroomLists = await findChatroomWithName(
          activeSenderRoleObject.object.id
        );
        setChatroomList(chatroomLists);
      };

      loadchatroomList();
    }
  }, [activeSenderRoleObject]);

  useEffect(() => {
    const _roleSelectionList = roleObjects
      .filter((e) => e.get("rolePublicProfile"))
      .map((e, index) => ({
        value: e.get("name"),
        label: e.get("rolePublicProfile").get("name"),
        object: e,
        index,
      }));
    setRoleSelectionList(_roleSelectionList);
    if (!activeSenderRoleObject) {
      setActiveSenderRoleObject(_roleSelectionList && _roleSelectionList[0]);
    }
  }, [roleObjects]);

  return (
    <Box height="100%" overflow="scroll">
      <SelectRole
        initialRoleIndex={
          activeSenderRoleObject ? activeSenderRoleObject : false
        }
        list={roleList}
        onSelectRole={(option) => {
          onRoleChange && onRoleChange(option);
          set_InitialChatroom(-1);
        }}
      />

      <ChatNavigator
        chatroomList={chatroomList}
        activeSenderRoleObject={activeSenderRoleObject}
        chatroomIndex={_initialChatroom}
        onBackAnchor={() => set_InitialChatroom(-1)}
        onChatroomSelected={(chatroom, index) => {
          set_InitialChatroom(index);
        }}
      />
    </Box>
  );
};

export default Component;
