import React, { useState, useEffect } from "react";
import Layout from "./layout";
import UserContext from "../context/UserContext";
import Parse, {
  getMyRoles,
  getMyRoleObject,
  getChatroomForRoles,
} from "../library/parse";
import NotificationContext from "../context/NotificationContext";
import ChatWidget from "../containers/ChatWidget";
import NewChat from "../containers/NewChat";
import ChatContext, { defaultValue } from "../context/ChatContext";

const Component = ({ children }) => {
  const [showChat, setShowChat] = useState(false);
  const [_userContext, setUserContext] = useState(null);
  const [chatProps, setChatProps] = useState({});
  const [roleList, setRoleList] = useState([]);
  const [notificationContext, setNotificationContext] = useState({
    maximise: () => {
      setShowChat(true);
    },
    minimise: () => {
      setShowChat(false);
    },
  });
  const [chatContextValue, setChatContextValue] = useState({
    openChat: async (sender, recipient, message) => {
      if (sender && recipient) {
        const chatroom = await getChatroomForRoles(sender, recipient);
        if (chatroom) {
          setShowChat(true);
          setChatProps({
            senderRole: sender,
            initialChatroom: chatroom,
            message,
          });
        }
      } else {
        setShowChat(true);
      }
    },
  });
  const loadUser = async (user) => {
    const ros = await getMyRoleObject();
    const userRole = ros.find((ro) => ro.get("name") === `User-${user.id}`);
    if (user && userRole) {
      setUserContext({
        user,
        roles: await getMyRoles(),
        roleObjects: ros,
        config: userRole.get("config") || {},
        refresh: () => {
          const user = Parse.User.current();
          if (user) {
            loadUser(user);
          }
        },
      });
    }
  };

  useEffect(() => {
    setChatProps({
      senderRole: _userContext && `User-${_userContext.user.id}`,
    });
    setRoleList(
      _userContext
        ? _userContext.roleObjects
            .filter((e) => e.get("rolePublicProfile"))
            .map((e, index) => ({
              value: e.get("name"),
              label: e.get("rolePublicProfile").get("name"),
              object: e,
              index,
            }))
        : []
    );
  }, [_userContext]);
  useEffect(() => {
    const user = Parse.User.current();
    if (user) {
      loadUser(user);
    }
  }, []);
  return (
    <UserContext.Provider value={_userContext}>
      <NotificationContext.Provider value={notificationContext}>
        <ChatContext.Provider value={chatContextValue}>
          <Layout>{children}</Layout>
          {_userContext && "" && (
            <ChatWidget
              onClick={() => setShowChat(!showChat)}
              expandContainer={showChat}
            >
              {
                <NewChat
                  {...chatProps}
                  onRoleChange={(option) => {
                    setChatProps({
                      senderRole: option.value,
                    });
                  }}
                  roleList={roleList}
                />
              }
            </ChatWidget>
          )}
        </ChatContext.Provider>
      </NotificationContext.Provider>
    </UserContext.Provider>
  );
};

export default Component;
