export const fblogIn = (cb, scope) => {
  return FB.login(
    function (response) {
      const { authResponse } = response;
      if (authResponse) {
        const authData = {
          facebook: {
            id: authResponse.userID,
            access_token: authResponse.accessToken,
            expiration_date: new Date(
              authResponse.data_access_expiration_time * 1000
            ).toISOString(),
          },
        };
        return FB.api(
          "/me?fields=id,name,email,permissions",
          function (response) {
            return Parse.Cloud.run("socialLogin", {
              authData,
              fullName: response.name,
              email: response.email,
            }).then((user) => {
              Parse.User.become(user.getSessionToken()).then((e) => cb && cb());
            });
          }
        );
      }
    },
    {
      scope,
    }
  );
};

